.PostsItem {
    width: 21%;
    padding: 20px;
}
.PostsCard {
    position: relative;
    height: 100%;
    color: #424242;
    background-color: #fff;
    border-radius: 6px;
    overflow: hidden;
    -webkit-box-shadow: 5px 5px 25px 0 rgba(46,61,73,.2);
            box-shadow: 5px 5px 25px 0 rgba(46,61,73,.2);
    border: 3px solid #424242;
    -webkit-transition: -webkit-box-shadow .3s ease-out;
    transition: -webkit-box-shadow .3s ease-out;
    -o-transition: box-shadow .3s ease-out;
    transition: box-shadow .3s ease-out;
    transition: box-shadow .3s ease-out, -webkit-box-shadow .3s ease-out;
}
.PostsItem__img {
    display: block;
    width: 100%;
    height: 200px;
    margin-bottom: 10px;
    -o-object-fit: cover;
       object-fit: cover;
}
.PostsItem_info {
    padding: 8px;
}
.PostsItem__popularity {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #424242;
    color: #fff;
    width: 60px;
    height: 45px;
    font-weight: 700;
    line-height: 45px;
    border-right: 2px solid #fff;
    border-bottom: 2px solid #fff;
    text-align: center;
    border-bottom-right-radius: 35%;
}
.PostsItem__title {
    margin-bottom: 16px;
    font-weight: 700;
    font-size: 1.3em;
}
.PostsItem__overview{
    margin-bottom: 20px;
    font-size: 14px;
    line-height: 1.3;
}
.PostsItem__rate {
    margin-bottom: 16px;
    font-size: 16px;
}
.PostsItem__add {

    position: absolute;
    display: block;
    outline: 0;
    border: 0;
    background-color: #424242;
    cursor: pointer;
    color: #fff;
    font-weight: 700;
    bottom: 8px;
    right: 8px;
    width: 35px;
    height: 35px;
    font-size: 24px;
    line-height: 31px;
    border-radius: 4px;
    border: 2px solid #424242;
    -webkit-box-shadow: 0 2px 2px 0 rgba(33,33,33,.5);
            box-shadow: 0 2px 2px 0 rgba(33,33,33,.5);
    -webkit-transition: all .1s linear;
    -o-transition: all .1s linear;
    transition: all .1s linear;
}
.PostsItem__add:hover {
    -webkit-box-shadow: 0 4px 2px 0 rgba(33,33,33,.5);
            box-shadow: 0 4px 2px 0 rgba(33,33,33,.5);
    background-color: #fff;
    color: #424242;
}










