.StackList {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    list-style: none;
}
.StackList__item {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    width: 10%;
    padding: 20px 10px;
    border: 3px solid #424242;
    border-radius: 4px;
}
.StackList__icon {
    display: block;
    overflow: hidden;
    width: 100px;
    height: 100px;
    margin: auto;
    background-position: 50%;
    background-size: cover;
}



